import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Modal from "react-modal"; //  react modal  .
import "../css/WeekPopup.css";
import axios from "axios";

const customStyles = {
  overlay: {
    zIndex: 10, 
  },
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    backgroundColor: "white",
    position: "fixed",
    width: "400px", 
  },
};

Modal.setAppElement("#root");

const WeekPopup = ({ onClose }) => {
  const [isRemembered, setIsRemembered] = useState(false);
  const [book, setBook] = useState(null); //    
  const navigate = useNavigate();

  useEffect(() => {
    //      
    const isLoggedIn = sessionStorage.getItem("isLoggedIn");

    //   true     
    if (isLoggedIn !== "true") {
      navigate("/");
    }
    else{
      axios
      .get(`/api/aladin/1`, {
        params: {
          Query: "", //   .
          QueryType: "Title", //   'Title' .
        },
      })
      .then((response) => {
        setBook(response.data.item[0]); //    .
      })
      .catch((error) => {
        console.log(error);
      });
    }
    //  '   '  
    const dontShowToday = localStorage.getItem("dontShowToday");
    if (dontShowToday === "true") {
      onClose();
    }
  }, []);

  const handleCheckboxChange = (e) => {
    setIsRemembered(e.target.checked);
  };

  const handleDontShowToday = () => {
    localStorage.setItem("dontShowToday", "true"); //     
    onClose();
  };

  const disableBodyScroll = () => {
    document.body.style.overflow = 'hidden';
  };

  const enableBodyScroll = () => {
    document.body.style.overflow = 'auto';
  };

  return (
    <Modal
      isOpen={sessionStorage.getItem("isLoggedIn") === "true"}
      onRequestClose={() => {
        enableBodyScroll(); //    
        onClose();
      }} // (   esc )
      style={customStyles}
      contentLabel="modal"
    >
      <div className="week">
        <div className="week-book">
          <h4> </h4>
        </div>
        <span className="close" onClick={onClose}>
          &times;
        </span>
      </div>
      <hr />
      <div className="book">
      {book && (
          <>
            <div className="book-cover_w">
              <img src={book.cover} alt={book.title} />
            </div>
            <div className="book-explain">
              <div className="book-title_w">{book.title}</div>
              <div className="book-author_w">{book.author}</div>
              <div className="book-review_w">{book.description}</div>
            </div>
          </>
        )}
      </div>
      <hr />
      <div className="check-close">
        <input
          type="checkbox"
          id="remember"
          checked={isRemembered}
          onChange={handleCheckboxChange}
        />
        <label htmlFor="remember">   </label>
      </div>
    </Modal>
  );
};

export default WeekPopup;
