import React, { useEffect, useState } from "react";
import axios from "axios";

function BookSearch() {
  const [books, setBooks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [categoryId, setCategoryId] = useState("");

  const handleSelectChange = (e) => {
    e.persist();
    setCategoryId(e.target.value);
  };

  const handleSearch = () => {
    if (!categoryId) return;

    const categoryNames = {
      1: "",
      53512: "",
      656: "",
      53490: "",
      53481: "",
      90456: "",
      53532: "",
      170: "",
      48840: "",
      77389: "",
      8516: "",
      53551: "",
      51279: "",
      17436: "",
      8369: "",
      8467: "",
      51377: "",
      54889: "",
      4132: "",
      50951: "",
      3741: "",
      3560: "",
      2551: "",
    };

    const query = categoryNames[categoryId]; //  id   .

    setLoading(true);
    // console.log("   :", categoryId); //   

    axios
      .get(`/api/aladin/${categoryId}`, {
        params: {
          Query: query, //   .
          QueryType: "Title", //   'Title' .
        },
      })

      .then((response) => {
        // console.log("API ", response.data); //   
        setBooks(response.data.item);
        setLoading(false);
      })
      .catch((error) => {
        console.log(error);
        setLoading(false);
      });
  };

  if (loading) {
    return <div>..</div>;
  }
  if (!books) {
    return null;
  }

  return (
    <div>
      <select onChange={handleSelectChange}>
        <option value="">--   --</option>
        <option value="1"></option>
        <option value="53512"></option>
        <option value="656"></option>
        <option value="53490"></option>
        <option value="53481"></option>
        <option value="90456"></option>
        <option value="53532"></option>
        <option value="170"></option>
        <option value="48840"></option>
        <option value="77389"></option>
        <option value="8516"></option>
        <option value="53551"></option>
        <option value="51279"></option>
        <option value="17436"></option>
        <option value="8369"></option>
        <option value="8467"></option>
        <option value="51377"></option>
        <option value="54889"></option>
        <option value="4132"></option>
        <option value="50951"></option>
        <option value="3741"></option>
        <option value="3560"></option>
        <option value="2551"></option>
      </select>
      <button onClick={handleSearch}></button>
      {books &&
        books.map((book) => (
          <div key={book.isbn}>
            <h2>{book.title}</h2>
            <img src={book.cover} alt={book.title} />
          </div>
        ))}
    </div>
  );
}

export default BookSearch;
