import React from "react";
import "../css/Main_Header.css";
import { Link } from "react-router-dom";
import Mypage from "../pages/Mypage";

//  
const logo = "/images/ccc_image/logo.png";
const search = "/images/ccc_image/search.png";
const books = "/images/ccc_image/books.png";
const user = "/images/ccc_image/user.png";
const upArrow = "/images/ccc_image/up-arrow.png";

const userid = JSON.parse(sessionStorage.getItem("userid"));

class Header extends React.Component {
  headerRef = React.createRef(); // 

  state = {
    search: "",
    isMypageOpen: false,
    isScrolled: false, // 
  };
  //    .
  toggleMypage = () => {
    const isLoggedIn = sessionStorage.getItem("isLoggedIn");

    if (isLoggedIn !== "true") {
      alert(" .");
    } else {
      this.setState((prevState) => ({
        isMypageOpen: !prevState.isMypageOpen,
      }));
    }
  };

  handleChange = (event) => {
    this.setState({ search: event.target.value });
  };

  handleScroll = () => {
    const totalHeight = this.headerRef.current
      ? this.headerRef.current.offsetHeight
      : 0;
    if (window.pageYOffset > totalHeight) {
      this.setState({ isScrolled: true });
    } else {
      this.setState({ isScrolled: false });
    }
  };

  handleClick = (event) => {
    event.preventDefault();

    const targetId = event.target.getAttribute("href");
    const targetElement = document.querySelector(targetId);

    if (targetElement) {
      // targetElement   
      const targetPosition =
        targetElement.getBoundingClientRect().top + window.pageYOffset;

      //    .  , 60px .
      const headerHeight = 109;

      window.scrollTo({
        top: targetPosition - headerHeight,
        behavior: "smooth",
      });
    }
  };

  componentDidMount() {
    this.handleScroll();
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  render() {
    // console.log(" .");
    console.log(": ", userid);
    return (
      <React.Fragment>
        {!this.state.isScrolled && (
          <header className="header_normal" ref={this.headerRef}>
            <div className="header_inner" id="inner1">
              <div className="logo" onClick={() => window.location.reload()}>
                <img src={logo} alt="" />
              </div>

              <div className="search_wrap">
                <div className="search_box">
                  <div className="search_">
                    <input
                      type="text"
                      id="search"
                      name="search"
                      placeholder="  ."
                      className="input_text_mh"
                      value={this.state.search}
                      onChange={this.handleChange}
                      maxLength="20"
                      autoCapitalize="off"
                    />
                    <span>
                      <Link to={`/search?query=${this.state.search}`}>
                        <button type="search_button" id="searchbooks">
                          <img src={search} alt="search_button" />
                        </button>
                      </Link>
                    </span>
                  </div>
                </div>
              </div>
              <div className="menu_wrap">
                <div>
                  <Link to="/register">
                    <button className="register_"></button>
                  </Link>
                  <span>|</span>
                  <Link to="/login">
                    <button className="login_"></button>
                  </Link>
                </div>
                <div className="library_icon">
                  <Link to="/mylibrary">
                    <button type="books_button" id="mybooksmove">
                      <img src={books} alt="search_button" />
                    </button>
                  </Link>
                </div>
                <div className="mypage_icon">
        <button
          className="user_button"
          id="userpagemove1"
          onClick={this.toggleMypage}
        >
          <img src={user} alt="search_button" />
        </button>
      </div>
      {this.state.isMypageOpen && (
        <Mypage isMypageOpen={this.state.isMypageOpen} onClose={this.toggleMypage} />
      )}
              </div>
            </div>
          </header>
        )}
        {this.state.isScrolled && (
          <header
            className={this.state.isScrolled ? "header_fixed" : "header_normal"}
          >
            <div className="header_inner">
              <div className="logo" onClick={() => {
                window.scrollTo(0, 0);
              }}>
                <img src={logo} alt="" />
              </div>
              <div className="search_wrap">
                <div className="search_box">
                  <div className="search_">
                    <input
                      type="text"
                      id="search"
                      name="search"
                      placeholder="  ."
                      className="input_text_mh"
                      value={this.state.search}
                      onChange={this.handleChange}
                      maxLength="20"
                      autoCapitalize="off"
                    />
                    <span>
                      <Link to={`/search?query=${this.state.search}`}>
                        <button type="search_button" id="searchbooks">
                          <img src={search} alt="search_button" />
                        </button>
                      </Link>
                    </span>
                  </div>
                </div>
                <nav className="small_nav_2">
                  <ul>
                    <li>
                      <a onClick={(e) => {
                        window.scrollTo(0, 0);
                      }}>
                         
                      </a>
                    </li>
                    <span>|</span>
                    <li>
                      <a href="#monthbooks_wrap" onClick={this.handleClick}>
                         
                      </a>
                    </li>
                    <span>|</span>
                    <li>
                      <a href="#soaring_wrap" onClick={this.handleClick}>
                        
                      </a>
                    </li>
                  </ul>
                </nav>
              </div>
              <div className="menu_wrap">
                <div>
                  <Link to="/register">
                    <button className="register_"></button>
                  </Link>
                  <span>|</span>
                  <Link to="/login">
                    <button className="login_"></button>
                  </Link>
                </div>
                <div className="library_icon">
                  <Link to="/mylibrary">
                    <button type="books_button" id="mybooksmove">
                      <img src={books} alt="search_button" />
                    </button>
                  </Link>
                </div>
                <div className="mypage_icon">
                  <button
                    className="user_button"
                    id="userpagemove2"
                    onClick={this.toggleMypage}
                  >
                    <img src={user} alt="search_button" />
                  </button>
                </div>
              </div>
            </div>
          </header>
        )}
        <a href="#" className="top-button" onClick={this.scrollToTop}>
          <img src={upArrow} alt="Top " />
        </a>
        <Mypage
          isMypageOpen={this.state.isMypageOpen}
          onClose={this.toggleMypage}
        />
      </React.Fragment>
    );
  }

  //    .
  scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
}

export default Header;
